import React from 'react'
import {Fab} from '@material-ui/core';

const styles = {
    headerTitle: {
        margin: '0',
        fontSize: '30px',
        textAlign: 'center',
        lineHeight: '70vh',
        color: '#fcedd8',
        fontFamily: 'Niconne, cursive',
        fontWeight: '700',
        textShadow: '5px 5px 0px #eb452b, 10px 10px 0px #efa032, 15px 15px 0px #46b59b, 20px 20px 0px #017e7f, 25px 25px 0px #052939, 30px 30px 0px #c11a2b, 35px 35px 0px #c11a2b, 40px 40px 0px #c11a2b, 45px 45px 0px #c11a2b',
    },
    checkGenders: {
        height: '48px',
        textAlign: 'center',
        whiteSpace: 'nowrap'
    }
};

export default function portada() {
    return (
        <div>
            <h2 style={styles.headerTitle}>The Gender Tracker</h2>
            <div className="centered">
                <Fab href="./list" style={styles.checkGenders} variant="extended" color="primary">
                    Check out all genders!
                </Fab>
            </div>
        </div>
    )
}
