import './App.css';
import Portada from './components/Portada';
import GenderList from './components/GenderList';
import {
  BrowserRouter as Router,
  Switch,
  Route,
} from "react-router-dom";


function App() {
  return (
    <Router basename={process.env.PUBLIC_URL}>
      <div>
        <Switch>
          <Route path="/list">
            <GenderList />
          </Route>
          <Route path="/">
            <Portada />
          </Route>
        </Switch>
      </div>
    </Router>
  );
}

export default App;
