import React from 'react';
import { makeStyles } from '@material-ui/core/styles';
import {List, ListItem, ListItemText} from '@material-ui/core'

const useStyles = makeStyles((theme) => ({
    root: {
        width: '100%',
        position: 'absolute',
        overflow: 'auto',
        textAlign: 'center',
        height: '80%'
      },
    center: {
        textAlign: 'center',
        justifyContent: 'center',
    },
    itemList: {
        textAlign: 'center',
        ...theme.typography.button,
        backgroundColor: 'rgba(255,255,255,0.5)',
        padding: theme.spacing(1),
        maxWidth: '150px',
        color: 'black',
        fontFamily: 'Niconne, cursive',
        fontWeight: '1000',
    },
    title: {
        margin: 0,
        paddingTop: 20,
        paddingBottom: 20,
        fontSize: '30px',
        textAlign: 'center',
        color: '#fcedd8',
        fontFamily: 'Niconne, cursive',
        fontWeight: '700',
        textShadow: '5px 5px 0px #eb452b',
    }
}));

const genders = [
      "Agender",
      "Androgynous",
      "Bigender",
      "Demigirl",
      "Demiguy",
      "Feminine",
      "Femme",
      "Genderqueer",
      "Genderfluid",
      "Intersex",
      "Man",
      "Masculine",
      "Neutrois",
      "Nonbinary",
      "Other",
      "Pangender",
      "Third Gender",
      "Woman",
];

export default function GenderList() {
    const classes = useStyles();
    return (
        <div className="genderList">
            <div className={classes.center}>
                <h2 className={classes.title}>This is the actual list of genders.</h2>
                <List className={classes.root}>
                    {genders.map(function(d, idx){
                        return (<ListItem className={classes.center} key={idx}>
                                <ListItemText className={classes.itemList} primary={d} />
                            </ListItem>)
                    })}
                </List>
            </div>
        </div>
    )
}
